:root {
  --dark-grey: #3a4663;
  --dark-blue: #181f33;
  --light-blue: #647198;
  --light-blue-shading: #414e73;
  --white: #fff;
  --grey: #434a59;
  --blue: #242d44;
  --beige: #eae3dc;
  --beige-shading: #b3a497;
  --accent-red: #d03f2f;
  --accent-red-shading: #93261a;
}

.body {
  background-color: var(--dark-grey);
  color: var(--white);
  margin: 0;
  font-family: League Spartan, sans-serif;
  font-weight: 700;
}

.link {
  color: var(--dark-blue);
  letter-spacing: -.001em;
  text-decoration: none;
}

.link:hover, .link:focus {
  color: var(--accent-red);
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  min-width: 320px;
  max-width: 375px;
  padding-left: 24px;
  padding-right: 24px;
}

.attribution {
  padding-top: 30px;
  padding-bottom: 20px;
}

.fe-mentor {
  margin-bottom: 10px;
}

.header {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 32px;
}

.header-content {
  gap: 130px;
  display: flex;
}

.app-title {
  margin: 0;
  padding-top: 10px;
  padding-bottom: 3px;
  font-size: 32px;
}

.theme {
  align-items: end;
  gap: 26px;
  display: flex;
}

.theme-title {
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 5px;
  font-size: 12px;
}

.switcher {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.switcher-position {
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 12px;
  display: flex;
}

.slider-btn {
  position: relative;
}

.slider-btn:before {
  content: "";
  background-color: var(--blue);
  height: 26px;
  width: 71px;
  border-radius: 50px;
  margin: auto;
  display: inline-block;
  top: 0;
  bottom: 0;
  box-shadow: 0 0 4px rgba(0, 0, 0, .494);
}

.slider-btn:after {
  content: "";
  background-color: var(--accent-red);
  height: 16px;
  width: 16px;
  border-radius: 50%;
  margin: auto;
  transition: all .4s;
  display: inline-block;
  position: absolute;
  top: -20px;
  bottom: 0;
  left: 5px;
}

.toggle-checkbox {
  display: none;
}

.toggle-checkbox:checked ~ .slider-btn:before {
  background-color: var(--almost-white);
}

.toggle-checkbox:checked ~ .slider-btn:after {
  background-color: maroon;
  transform: translateX(11px);
}

.result {
  background-color: var(--dark-blue);
  text-align: right;
  border-radius: 10px;
  margin-top: 0;
  margin-bottom: 24px;
  padding-top: 29px;
  padding-bottom: 22px;
  padding-right: 24px;
  font-size: 40px;
}

.keyboard {
  background-color: var(--blue);
  border-radius: 10px;
  padding-top: 24px;
  padding-bottom: 24px;
}

.list-main-keys {
  flex-wrap: wrap;
  justify-content: center;
  gap: 13px;
  margin-bottom: 13px;
  display: flex;
}

.btn {
  width: 60px;
  height: 64px;
  box-shadow: 0px -4px 0px 0px var(--beige-shading) inset;
  background-color: var(--beige);
  color: var(--grey);
  border: none;
  border-radius: 5px;
  font-size: 32px;
}

.btn-delete {
  box-shadow: 0px -4px 0px 0px var(--light-blue-shading) inset;
  background-color: var(--light-blue);
  color: var(--white);
  text-transform: uppercase;
  font-size: 20px;
}

.list-action-btn {
  justify-content: center;
  gap: 13px;
  display: flex;
}

.btn-action {
  width: 133px;
  height: 64px;
  background: var(--light-blue);
  box-shadow: 0px -4px 0px 0px var(--light-blue-shading) inset;
  color: var(--white);
  border: none;
  border-radius: 5px;
  font-size: 20px;
}

.btn-reset {
  text-transform: uppercase;
}

.btn-equel {
  background: var(--accent-red);
  box-shadow: 0px -4px 0px 0px var(--accent-red-shading) inset;
  color: var(--white);
}

